<template>
    <main>
        <form action="#" method="post" @submit.prevent="checkingForm">
            <div class="titleWrapper">
                <h1>Войти</h1>
                <p class="welcome">Рады снова видеть Вас!</p>
            </div>
            <div class="wrapper__email">
                <span :class="{ active: isActiveEmail }" class="err">{{ emailErr }}</span>
                <input type="email" v-model="email" @blur="checkingEmail">
            </div>
            <div class="wrapper__password">
                <span :class="{ active: isActivePassword }" class="err">{{ passwordErr }}</span>
                <input type="password" v-model="password" @blur="checkingPassword">
                <img src="../assets/image/login/password.svg" alt="" @click="visibilityPas">
            </div>
            <button type="submit" title="Submit">Войти</button>
            <div class="navWrapper">
                <router-link id="registration" rel="nofollow" to="/registration">Регистрация</router-link>
                <router-link id="updatePassword" rel="nofollow" to="/password-update">Забыли пароль?</router-link>
            </div>
        </form>
    </main>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
    data(){
        return{
            password: '',
            email: '',
            isActiveEmail: false,
            isActivePassword: false,
            emailErr: '',
            passwordErr: ''
        }
    },
    computed: {
        ...mapGetters('UserModules', ['getUserData']),
    }, 
    mounted(){
        this.getUserData.userEmail === '' ? document.querySelector('.nav__block--right').style.display = 'none' : null
    },
    methods:{
        ...mapMutations('UserModules', ['setEmailAndPassword']),
        checkingEmail(e) {
            if(this.email.length > 0 && Boolean(this.email.match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)) !== false && this.email.includes('gmail') === true) {
                e.target.style.border = '1px solid rgba(38, 161, 123, 0.45)'
                this.isActiveEmail = false
                this.emailValid = true
            } else {
                e.target.style.border = '1px solid rgb(203 0 20 / 45%)'
                if(Boolean(this.email.match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)) === false){
                    this.emailErr = 'Неверный формат почты'
                    this.isActiveEmail = true
                } else if(this.email.includes('gmail') === false){
                    this.emailErr = 'Только почтовый сервис GMAIL'
                    this.isActiveEmail = true
                }
                this.emailValid = false
            }
        },
        checkingPassword(e){
            console.log(e+'pas')
        },
        checkingForm() {
            if(this.emailValid !== false && this.passwordValid !== false){
                this.$router.push('/')
                this.setEmailAndPassword({ email: this.email, password: this.password })
                console.log(this.password, this.getUserData)
            }
        },
        visibilityPas(){
            document.querySelector('.wrapper__password input').type === 'password' ? document.querySelector('.wrapper__password input').type = 'text' : document.querySelector('.wrapper__password input').type = 'password'
        }
    }
}
</script>

<style lang="scss" scoped>
    main{
        background: #EEF5FE;
        padding: 30px 0;
    }
    .err{
        display: none;
    }
    form{
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 410px;
        height: 444px;
        background: #FFFFFF;
        border-radius: 5px;
    }

    button{
        width: 314px;
        height: 40px;
        background: #42239A;
        border-radius: 5px;
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        color: #FFFFFF;
        border: none;
        margin-bottom: 39px;
    }

    h1{
        font-weight: 500;
        font-size: 25px;
        line-height: 29px;
        color: #000000;
        margin: 41px 0 10px;
    }
    .titleWrapper{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 314px;
    }
    .welcome{
        font-weight: 300;
        font-size: 14px;
        line-height: 16px; 
        color: #000000;
    }

    p{
        font-weight: 300;
        font-size: 13px;
        line-height: 130%;
        color: #000000;
        text-align: center;
    }

    a{
        font-weight: 300;
        font-size: 13px;
        line-height: 130%;
        color: #42239A;
    }
    .navWrapper{
        display: flex;
        justify-content: space-between;
        width: 314px;
    }
    .active{
        display: block;
        position: absolute;
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 13px;
        color: #CB0014;
        right: 10px;
        top: -17px;
    }

    #registration,
    #updatePassword{
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        color: #42239A;
        margin-bottom: 29px;
    }

    input[type=password],
    input[type=text],
    input[type=email]{
        width: 314px;
        height: 47px;  
        border: 1px solid#E9E9E9;
        border-radius: 5px;
        padding: 0 105px 0 35px;
        font-weight: 400;
        font-size: 16px;
        line-height: 18px;
        color: #000000;
    }

    .wrapper__email{
        position: relative;
        margin: 20px 0;
        &::before{
            content: url(../assets/image/registration/Mail.svg);
            position: absolute;
            top: 14px;
            left: 11px;
        }

        &::after{
            content: 'Email';
            width: 105px;
            height: 47px;
            font-weight: 300;
            font-size: 14px;
            line-height: 16px;
            position: absolute;
            top: 0;
            right: 0;
            display: flex;
            align-items: center;
            text-align: center;
            color: rgba(163, 163, 163, 0.5);
            justify-content: center;
        }
    }
    .wrapper__password{
        position: relative;
        margin-bottom: 34px;
        &::before{
            content: url(../assets/image/registration/Password.svg);
            position: absolute;
            top: 15px;
            left: 11px;
        }

        img{
            position: absolute;
            top: 14px;
            right: 34px;
            &:hover{
                cursor: pointer;
            }
        }
    }

    @media  screen and (max-width:480px){
        form{
            width: 100%;
        }
    }
</style>